
.right-navbar{
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 280px;
    overflow-y: auto;
}

.sb{
    justify-content: space-between;
}

.Contacts-box-icons div,
.Contacts-box-h4{
    padding: 8px;   
    margin-left: 4px;
}
.Contacts-box{
    justify-content: space-between;
    margin-top: 16px;
    margin-right: 4px;
}
.Contacts-box-icons div{
    border-radius: 50%;
    cursor: pointer;
}
.Contacts-box-icons div:hover{
    background-color: #E4E6E9;
   
}
.plus-icon{
    padding: 8px;
    background-color: #ccced1;
    border-radius: 50%;
    margin-right: 8px;
}
.plus-icon img{
    display: block;
    width: 24px;
    height: 24px;

}
.Group-icon{
    margin-bottom: 16px;
}
.Group-icon .h4{
    padding-top: 20px;
}
.Group-icon-desc{
   align-self: center;
}

