.post-box{
    width: 500px;
    margin: 16px 0;
    background-color: #fff;
    border-radius: 8px;
    padding: 12px 0 8px;
    font-size: 14x;
    box-shadow: 0 0 3px rgba(0, 0, 0,.4);
}
.post-box-top{
    padding: 0 16px;
    justify-content: space-between;
}
.prof-img-name{
    margin-left: 8px;
}
.time-b{
    margin-top: auto;
    font-size: 12px;
    color: rgba(0, 0, 0,.5);
    margin-top: 5px;
}
.post-more-i{
    font-size: 18px;
    color: rgba(0, 0, 0,.5);
    align-self: center;
    border-radius: 50%;
    padding: 9px 12px;
}
.post-more-i:hover{
    background-color: #F2F2F2
}
.unl:hover{
    text-decoration: underline;
}
.post-desc{
    padding: 0 20px;
}
.post-gallery{
    padding: 8px 0;
}
.post-gallery img{
    max-width: 100%;

}