.Like-pannel{
    padding: 0 16px;
    color: rgba(0, 0, 0,.4);
    font-size: 16px;
    font-weight: 600;
}
.like-pannel-count{
    padding: 8PX 0 ;
    justify-content: space-between;
}

.border-bottom{
    border-bottom: 1px solid #CED0D4;
}
.likeButtons{
    padding: 4px;
}
.likeButtons div{
 flex-grow: 1;
 text-align: center;
 padding: 8px;
}
.likeButtons div:hover{
    background-color: #E4E6E9;
    border-radius: 8px;
}
.like-count img{
    width: 16px;
    height: 16px;
}
.like-count .img-1{
    margin-right: -2px;
    position: relative;
    z-index: 1;

}
.like-count .img-1 img{
    width: 20px;
    height: 20px;
    border: 2px solid #E4E6E9;
    border-radius: 50%;
}
.like-count .img-2{
    margin-right: 4px;
}