
.left-navbar{
position: fixed;
top: 0;
left: 80;
height: 100%;
width: 280px;
overflow-y: auto;
padding-left: 8px;

}
.heading-h2{
    padding: 16px 0 8px 16px;
    
}

.left-navbar-item-box{
    font-size: 14px;
    font-weight: 500;
    padding: 10px;
    border-radius: 8px 0 0 8px;
    cursor: pointer;
}
.left-navbar-item-box:hover{
    background-color:#E4E6E9;
}
.brand{
    padding: 20px 0 0 10px;
}
.left-nav-icon{
    padding-right: 14px;
}
.left-nav-icon img{
    width: 20px;
    height: 20px;
}
.left-icon-text{
    align-self: center;
}

.see-more{
    display: none;
}
.see-more:hover{
    background-color: #E4E6E9;
}
.left-nav-footer{
    padding: 8px 0;
}
.left-nav-footer,
.left-nav-footer a{
    font-size: 12px;
    color: rgba(0, 0, 0,.5);
}
.left-nav-footer a:hover{
    text-decoration: underline;

}
.block{
    display: block;
  }
  .rotat{
    transform: rotate(180deg);
  }