.leftMenu{
    font-size: 24px;
    text-align: center;
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    padding: 16px 10.5px;
    border-right: 1px solid lightgray;
    background-color: #fff;
}
.leftMenu > div{
    margin-top: 8px;
    border-radius: 8px;
}
.leftMenu img{
    width: 35px;
    height: 35px;
}
.profil-img{
    padding:  0 0;
    position: relative;
    cursor: pointer;
}
.profil-img img{
    border-radius: 50%;
    
}
.img-badge img{
    width: 20px;
    height: 20px;
    border: 2px solid #fff;
}
.img-badge{
    position: absolute;
    bottom: -2px;
    right: 6px;
    z-index: 2;
}
.leftMenu li{
    padding: 12px;
    cursor: pointer;  
}
.leftMenu li:hover{
    background-color: #f1f1f1;
    border-radius: 8px;
}
.bg-color-grey{
    background-color: #F0F2F5;
}
.bg-color-grey li:hover{
    background-color:#E4E6E9;
}