
.card-div{
    position: relative;
    margin: 30px 20px;
}
.add-story{
    width: 112px;
    height: 198px;
    overflow: hidden;
    border-radius: 8px;
    font-size: 14px;
    cursor: pointer;
    position: relative;
    margin: 0 5px;
}
.add-story img{
    width: auto;
    height: 100%;
    transition: all .3s;
   
}

.add-story:hover  img{
    transform: scale(1.05);
   }
.add-story-desc{
    height: 48px;
    background-color: #fff;
    text-align: center;
    padding-top: 24px;
}
.add-story-img{
    height: 150px;
    position: relative;
}

.bg-plus{
    position: relative;
    top: 0;
    left: 0;
}
.bag-1{
    position: absolute;
    bottom: -25px;
    left: 35px;
    border: 4px solid #fff;
    border-radius: 50%;
    color:  #fff;
    padding: 11px 12px;
    background-color: #1876f2;
}
div.str-badge{
    position: absolute;
    top: 5px;
    left: 5px;
    z-index: 5;
    
}
.str-badge img{
    border: 4px solid #1876f2;
    border-radius: 50%;
    width: 40px;
    height: 40px;

}
.add-story:hover .str-badge img{
    opacity: 1;
}

.more-bdg{
    position: absolute;
    top: 40%;
    right: -20px;
    color: rgba(0, 0, 0,.6);
    background-color: #fff;
    padding: 10.5px 12px;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 0 2px 2px rgb(0,0,0,.16);
}
.overlayer{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    background-color: rgba(48, 47, 47,.3);
    justify-content: center;
   align-items: flex-end;
   padding: 4px;
    
}
