.add-post{
    width: 500px;
    padding: 16px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 3px rgba(0, 0, 0,.4);
}

.top{
 border-bottom: 1px solid rgba(128, 128, 128,.4);
 padding-bottom: 8px;
}
.prof-img img{
    border-radius: 40px;
    cursor: pointer;
}
.search {
    align-self: center;
}
.search input{
    width: 420px;
    margin-left: 10px;
    padding: 12px;
    border-radius: 40px;
    border: none;
    background-color: #F0F2F5;
    font-size: 18px;
    cursor: pointer;
}
.search input:hover{
    background-color: "#E4E6E9";
}
.search input:focus{
    outline: none;
}
.add-item{
    align-items: center;
    flex-grow: 1;
    margin-top: 8px;
    padding: 8px 0 5px;
    cursor: pointer; 
    color: #657174;
    
}
.add-item:hover{
    background-color: #F2F2F2;
    border-radius: 8px;
}
.atteh-icon{
    margin: 0 8px 0 16px;
}