
.onlion-user{
    padding: 8px; 
    cursor: pointer;
    border-radius: 8px;
}
.onlion-user:hover{
    background-color: #E4E6E9;   
}
.onlion-user-img{
    width: 24px;
    height: 24px;
    margin-right:  12px;
    position: relative;
}
.onlion-user-img img{
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
   
    
}
.onlin-user-name{
    vertical-align: middle;
}
.onlion-icon{
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 5;

}
.onlion-icon img{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 1px solid #fff;
    
}