
.card-item a{
    display: block;
    padding: 8px;
    color: #000;
   
}
.card-item p{
    font-size: 12px;
    color: rgba(0, 0, 0,.5);
}
.card-item{
    position: relative;
}
.card-item a:hover{
    background-color: #E4E6E9; 
    border-radius: 8px;
}
.card-item img{
    width: 100px;
    border-radius: 8px;
    margin-right: 8px;
}
.card-desc{
    align-self: center;
    padding-right: 4px;
}
.h4{
    padding: 32px 8px 8px ;
}
.card-menu img{
    display: block;
    margin: auto;
    width: 24px;
    align-items: middle;
   
}
.card-menu{
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 2;
    padding: 12px;
    box-shadow: 0 0 3px 2px  rgba(0,0,0,.16);
    display: none;
}
.card-menu:hover{
    background-color: #E4E6E9; 
    cursor: pointer;
}
.card-item:hover .card-menu{
    display: block;
}