.comment {
    margin: 0 16px;
    padding: 8px 0;
    color: rgba(0, 0, 0,.4);
}
.comment img{
    width: 32px;
    height: 32px;
  
}
.comment input{
    font-size: 14px;
    border-radius: 40px 0 0 40px;
    width: 295px;
    padding-right: 0;
}
.comment input:hover{
    background-color: #F0F2F5;
}
.search i{
    padding: 8px;
    border-radius: 50%;
}
.search i:hover{
    background-color: #CFD1D5;
}
.comment-icons{
    align-items: center;
    background-color: #F0F2F5;
    border-radius: 0 40px 40px 0;
    padding-right: 8px;
   
}
.prof-img {
    position: relative;
}
.onlionIcon img{
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: 2px solid #fff;

}
.onlionIcon{
    position: absolute;
    bottom: 4px;
    right: -2px;
}