.friend-request-card {
    height: 109px;
    position: relative;
}
.f-req-btn{
    position: absolute;
    left: 70px;
    bottom: 0;
    
    z-index: 2;

}
.f-req-btn button{
    padding: 8px 12px;
    font-size: 16px;
    border: none;
    margin: 10px;
    border-radius: 8px;
    cursor: pointer;
}


.confirm{
    background-color: #1B74E4;
    color: #fff;
}

.confirm:hover{
    background-color: #0e10b3;
}

.delete{
    background-color: #EBEDF0;
}

.delete:hover{
    background-color: #c2c3c4;
}

.mutual-f img{
    width: 16px;
    height: 16px;
    border-radius: 50%;

}
.fri-img1{
    padding-right: 1px;
    position: relative;
}
.fri-img2{
    position: absolute;
    top: 0;
    right: 60%;
}
img.img2{
    width: 18px;
    height: 18px;
    border: 2px  solid rgb(226, 226, 226);
}
.fri-img img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
}
.mutual-f{
    font-size: 12px;
    color: rgba(0, 0, 0,.4);
    margin-top: 8px;
}
.fri-name {
    width: 160px;
}
.friend-request-card {
    padding: 8px;
}
.friend-request-card:hover{
    background-color:#E4E6E9;
    border-radius: 8px;
}
.fri-img{
    margin-right: 16px;
}